import React, { useState, useEffect, useRef } from 'react';
import './App.scss';
import * as mapboxgl from 'mapbox-gl';
import AutoScale from 'react-auto-scale';
import axios from 'axios';
import { Dropdown, Input } from 'semantic-ui-react'
import Circle from "./layouts/Circle/index";
import 'semantic-ui-css/semantic.min.css'
import Default from "./layouts/Default";
import WithoutText from "./layouts/WithoutText/index";
import Rectangle from "./layouts/Rectangle";
import CircleWhite from "./layouts/CircleWhite";
import Square from "./layouts/Square";

const styles = {
  'blue': 'mapbox://styles/kubatyi/ckdn1lgk80nbp1il7tbjdqnxm',
  'red': 'mapbox://styles/kubatyi/ckdo7qi431s7n1io4ya13jmvq',
  'green': 'mapbox://styles/kubatyi/ckdogvd6721wt1ioii4wlls2a',
  'simple': 'mapbox://styles/kubatyi/ckdoglnu937ix1it80v32fsno'
};

const MAPBOX_TOKEN = 'pk.eyJ1Ijoia3ViYXR5aSIsImEiOiJjazA2cmtleDEwMTc5M2NwZ3N3Z2plcGdnIn0.Nio3ZaFI2366HS56tW28wQ';

const layoutOptions = [
  {
    key: '1',
    text: 'Gradient',
    value: 'gradient'
  },
  {
    key: '2',
    text: 'Without Text',
    value: 'without_text'
  },
  {
    key: '3',
    text: 'Circle',
    value: 'circle'
  },
  {
    key: '4',
    text: 'Circle (Light)',
    value: 'circle-white'
  },
  {
    key: '5',
    text: 'Rectangle',
    value: 'rectangle'
  },
  {
    key: '6',
    text: 'Square',
    value: 'square'
  }
];

const App = () => {
  const [cityInputValue, setCityInputValue] = useState('');
  const [cityDropdownItems, setCityDropdownItems] = useState([]);
  const [headlineText, setHeadlineText] = useState('');
  const [secondaryText, setSecondaryText] = useState('');
  const [layout, setLayout] = useState('gradient');
  const [map, setMap] = useState(null);
  const [center, setCenter] = useState([0, 0]);
  const cityInputRef = useRef();

  useEffect(() => {
    mapboxgl.accessToken = MAPBOX_TOKEN;

    setMap(new mapboxgl.Map({
      container: 'map',
      center: [0, 0],
      zoom: 11,
      style: styles[window.location.hash.substr(1)],
      attributionControl: false
    }));

  }, [layout]);

  useEffect(() => {
    if (map) {
      map.setCenter(center);
    }
  }, [map, center]);

  useEffect(() => {
    if (map) {
      map.dragRotate.disable();

      map.touchZoomRotate.disableRotation();

      map.on('touchend', sendStateToParent);
      map.on('zoomend', sendStateToParent);
      map.on('dragend', sendStateToParent);

      if (cityInputRef.current) {
        cityInputRef.current.searchRef.current.addEventListener('keyup', (e) => {
          onCityInputChange(e.target.value)
        })
      }

      window.addEventListener('message', (e) => {
        const data = JSON.parse(e.data);

        if (data.style) {
          if (map) {
            map.setStyle(styles[data.style]);
            window.location.hash = `#${data.style}`
          }
        }
      });
    }
  }, [map]);

  useEffect(() => {
    axios.get('https://api.ipapi.com/api/check?access_key=6d4c0f09aa78ad103b4927c2a7f00828')
      .then(res => {
        setHeadlineText(res.data.city);
        setSecondaryText(res.data.country_name);
        setCenter([res.data.longitude, res.data.latitude]);
        sendStateToParent();
      })
      .catch(e => {
        setHeadlineText('New York');
        setSecondaryText('United States');
        setCenter([-73.9808, 40.7648])
      });
  }, []);

  useEffect(() => sendStateToParent(), [map, headlineText, secondaryText]);

  const onCityInputChange = async value => {
    setCityInputValue(value);

    if (value.length) {
      const response = await axios.get(`http://api.mapbox.com/geocoding/v5/mapbox.places/${value}.json`, {
        params: {
          access_token: MAPBOX_TOKEN,
          autocomplete: true,
        }
      });

      const cities = response.data.features.map(el => {
        return {
          key: el.id,
          value: el.place_name,
          text: el.place_name,
          center: el.center,
          country: el.context ? el.context.map(el => el.text).join(', ') : ''
        }
      });

      setCityDropdownItems(cities);
    }
  };

  const sendStateToParent = () => {
    if (map) {
      const state = {
        zoom: map.getZoom(),
        center: map.getCenter(),
        headlineText,
        secondaryText,
        layout
      };

      window.parent.postMessage(JSON.stringify(state), '*');
    }
  };

  return (
    <div id="map-editor">
      <div className="inputs-wrapper">
        <Dropdown
          className="city-dropdown"
          ref={cityInputRef}
          placeholder='Search City'
          fluid
          search
          selection
          value={cityInputValue}
          options={cityDropdownItems}
          onChange={(e, data) => {
            console.log(data);
            const selectedCityObject = cityDropdownItems.find(el => el.text === data.value);
            map.flyTo({
              center: selectedCityObject.center,
              zoom: 12,
              speed: 3
            });
            console.log(selectedCityObject);
            setCenter(selectedCityObject.center);
            setHeadlineText(selectedCityObject.value.split(',')[0]);
            setSecondaryText(selectedCityObject.country);
            setCityInputValue(data.text);
          }}
        />
        <Dropdown
          className="city-dropdown"
          placeholder='Select Layout'
          fluid
          search
          selection
          options={layoutOptions}
          onChange={(e, data) => setLayout(data.value)}
        />
        <Input fluid label="Headline Text" disabled={layout === 'without_text'} value={headlineText} onChange={e => setHeadlineText(e.target.value)} />
        <Input fluid label="Secondary Text" disabled={layout === 'without_text'} value={secondaryText} onChange={e => setSecondaryText(e.target.value)} />
      </div>

      <AutoScale maxScale={1}>
        <React.Fragment>
          {layout === 'gradient' && <Default headlineText={headlineText} secondaryText={secondaryText} />}
          {layout === 'circle' && <Circle headlineText={headlineText} secondaryText={secondaryText} />}
          {layout === 'circle-white' && <CircleWhite headlineText={headlineText} secondaryText={secondaryText} />}
          {layout === 'without_text' && <WithoutText />}
          {layout === 'rectangle' && <Rectangle headlineText={headlineText} secondaryText={secondaryText}/>}
          {layout === 'square' && <Square headlineText={headlineText} secondaryText={secondaryText}/>}
        </React.Fragment>
      </AutoScale>
    </div>
  )
};

export default App;
